.CVPage {
    background-color: #F2F2F0;
    min-height: 12vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: start;
    font-size: calc(10px + 2vmin);
    font-weight: lighter;
    color: #0D0D0D;
    height:100%;
}

.EmbeddedPDF{
    min-height:fit-content;
    /*margin:2vmin;*/
}

.pdf {
    width: 100%;
    aspect-ratio: 4 / 3;
    height:100%;
    padding:0;
    margin: 0;
}

object{
    margin:0px;
}